.menu {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    transform: translate(100%);
    transition: 0.5s;
    display: flex;
}
.menu_active{
    transform: translate(0);
}
.blur{
    width: 80%;
    height: 100%;
    backdrop-filter: blur(2px);
}
.content{
    height: 100vh;
    max-width: 400px;
    width: 100%;
    background: #1C4A85;
    padding: 33px 3%;
    color: #FFFFFF;
}

.box_img{
    display: flex;
    justify-content: space-between;
}
.box_list{
    list-style: none;
    margin: 20% 0 0 0;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
}
.box_list > li{
    margin: 13px 0 0 0;
}
.box_social{
    margin: 25px 0;
}
.box_social > a:last-child{
    margin: 0 0 0 3%;
}
.box_listLan{
    list-style: none;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    display: flex;
}
.box_listLan > li:nth-child(2){
    margin: 0 4%;
}
.link{
    color: #FFFFFF;
}