.container{
    text-align: center;
    color: #FFFFFF;
}
.question1, .question2{
    margin: 59px 0 0 0;
}
.question1{
    font-size: 36px;
    line-height: 44px;
    font-style: italic;
}
.question2{
    font-weight: bold;
    font-size: 48px;
    line-height: 132.4%;
    line-height: 132.4%;
    letter-spacing: 0.075em;
}
.container_time{
    display: flex;
    justify-content: space-around;
    margin: 5% 20%;
}
.container_time > div{
    border-right: 1px solid #FFFFFF;
    width: 25%;
}
.container_time > div:last-child{
    border-right: none;
}
.box_time > p:first-child{
    font-style: normal;
    font-weight: bold;
    font-size: 800%;
    line-height: 134px;
}
.box_time > p:last-child{
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
}
@media (max-width: 768px) {
    .container_time {
        margin:  5% 0;
    }
    .box_time > p:first-child{
        font-size: 400%;
    }
    .question2 {
        font-size: 28px;
    }
}
@media (max-width: 500px) {
    .box_time > p:first-child{
        font-size: 300%;
    }
}