*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}
body{
  background: #E5E5E5;
}
.container_page{
  padding: 135px 0 0 0;
}
.wrapp{
  padding: 0 10% 0 133px;
}
.link{
  text-decoration: none;
}
.news_title{
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #000000;
}
#myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -99999;
}
@media(max-width: 650px){
  .wrapp{
    padding: 0 10%;
  }
  .news_title {
    text-align: center;
  }
}