.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}
.wrapper > * {
    width: 35px;
    height: 35px;
    font-size: 22px;
    margin: 0 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.7s linear;
}
.wrapper > div:hover {
    border: 2px solid #1C4A85;
}
.btn {
    background: #1C4A85;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn:focus {
    outline: none;
}
.btn > img {
    height: 22px;
    justify-content: center;
}
.active {
    border: 2px solid #1C4A85;

}