footer{
    margin: 100px 0 0 0;
    background: #1C4A85;
    padding: 32px 0;
    color: #FFFFFF;
}
footer > p {
    margin: 5% 0 0 0;
    text-align: center;
}
.footer{
    display: none;
}
.container{
    padding: 0 25% 0 133px;
    display: flex;
    align-items: center;
}
.list{
    margin: 0 0 0 5%;
    width: 100%;
    font-size: 18px;
    line-height: 22px;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
}
.list > li{
    margin: 2% 0 0 10%;
}

@media(max-width: 816px){
    .container{
        padding: 0 10% 0 5%;
    }
}
@media(max-width: 575px){
    .container > img{
        width: 56px;
        height: 31px;
    }
    .list{
        font-size: 12px;
    }
    footer > p{
        font-size: 14px;
        line-height: 17px;
    }

}