.container{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.container > div{
    margin: 40px 0 0 0;
}

@media(max-width: 1110px){
    .container{
        margin: 5% 10% 0;
    }
}
@media(max-width: 940px){
    .container > div{
        margin: 5% 20% 0;
    }
}
@media(max-width:  880px){
    .container > div{
        padding: 5% 0 0 0;
        margin: 0 auto;
    }
}
@media(max-width: 650px){
    .container{
      padding: 0;
    }
}
