.title{
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    text-align: center;
    color: #1C4A85;
}
.container{
    background: radial-gradient(103.01% 652.35% at -1.27% 3.74%, rgba(203, 203, 203, 0.22) 0%, rgba(205, 205, 205, 0.24) 100%);
    backdrop-filter: blur(10px);
    border-radius: 20px;
    width: 80%;
    margin: 0 0 0 133px;
    padding: 18px 10%;
}
.container > img {
    margin: 50px 0;
    max-width: 789px;
    width: 100%;
    height: 463px;
}