.container{
    padding: 53px 2% 43px 5%;
    background: #FFFFFF;
    box-shadow: 4px 4px 9px rgba(28, 74, 133, 0.25), -4px -4px 9px rgba(28, 74, 133, 0.25);
    border-radius: 20px;
    width: 100%;
    height: 308px;
}
.box_top{
    display: flex;
    align-items: center;
}
.box_top > img{
    max-width: 108px;
    width: 100%;
    height: 108px;
    border-radius: 100%;
}
.box_bottom{
    margin: 30px 0 0 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.box_text{
    margin: 0 0 0 5%;
}
.name__text{
    font-weight: 600;
    font-size: 22px;
    line-height: 29px;
}
.profession{
    margin: 16px 0;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #8C8C8C;
}
.box_process{
    margin: 0 0 0 160px;
    display: flex;
    align-items: center;
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    width: 237px;
    height: 67px;
    padding: 2%;
}
.process{
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    padding: 0 0 0 6%;
}
.time{
    font-weight: 600;
    font-size: 20px;
    line-height: 29px;
    color: #1C4A85;
}
.process{
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
}