nav{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url("../../assets/img/nav_bg.png");
    position: fixed;
    z-index: 3;
    max-width: 86px;
    width: 100%;
    height: 582px;
    padding: 43px 0;
    color: #FFFFFF;
}
.facebook{
    margin-top: 36px;
}
.instagram{
    margin-top: 27px;
}
.line{
    margin-top: 33px;
    width: 1px; 
    background-color: #FFFFFF;
    height: 127px;
}
.list{
    list-style: none;
    margin-top: 25px;
    list-style: none;
}
.list > li{
    margin-bottom: 28px;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
}
@media(max-width: 700px){
    nav{
        display: none;
    }
}
