header{
    padding: 31px 10% 0 133px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.header{
    position: fixed;
    background: #E5E5E5;
    z-index: 2;
    width: 100%;
}
.box{
    display: none;
}
.burgermobile{
    display: none;
}
.box > div:last-child{
    margin: 14px 0 0 0;
    position: relative;
}
.box > div:first-child{
    display: flex;
    align-items: center;
}
.loop{
    width: 13px;
    height: 14px;
    position: absolute;
    right: 5px;
    top: 3px;
}
.burger {
    cursor: pointer;
}
.search{
    border: 0.5px solid #FFFFFF;
    padding: 0 30px;
    width: 161px;
    height: 22px;
    background: #1C4A85;
}
.search:focus{
    outline: none;
}
.list{
    display: flex;
    list-style: none;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
}
.list > li{
    margin: 0 0 0 10px;
}
@media(max-width: 700px){
    header{
        padding: 11px 10%;
        background: #1C4A85 !important;
    }
    .burger{
        display: none;
    }
    .logo{
        display: none;
    }
    .box{
        display: block;
    }
    .logomobile{
        width: 60px;
        height: 30px;
    }
    .burgermobile{
        width: 40px;
        height: 15px;
        display: block;
    }
}