.container{
    max-width: 262px;
    width: 100%;
}
.container > img {
    width: 100%;
    height: 262px;
}
.time{
    text-align: end;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    color: #1C4A85
}
.text{
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
}
.text > p{
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
.link{
    margin: 0 0 0 65px;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: #1C4A85;
    text-align: end;
}
.link > span{
    text-decoration: underline;
}