.container{
    display: flex;
}
.container > img{
    max-width: 206px;
    width: 100%;
    height: 199px;
}
.box_text{
    margin: 0 6%;
    width: 100%;
}
.title{
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
}
.time_link{
    font-size: 12px;
    line-height: 15px;
    color: #1C4A85;
    text-align: end;
}
.text{
    font-size: 12px;
    line-height: 15px;
    display: -webkit-box;
    -webkit-line-clamp: 8;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
.margin_tb{
    margin: 2% 0;
}
.box{
    display: flex;
    justify-content: space-between;
}

@media (max-width: 768px) {
    .container > img{
        max-width: 150px;
        width: 100%;
        height: 150px;
    }
}