.title{
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    color: #1C4A85;
}
.list{
    margin: 24px 0 0 0;
    display: flex;
    list-style: none;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: #1C4A85;
}
.list > li{
    padding: 3px;
    margin: 0 0 0 3%;
    border: solid 1px;
    border-radius: 5px;
}
.list > li:first-child{
    margin: 0;
}
.list > li:nth-child(2){
    background: #EF861A;
    color: #FFFFFF;
}
.list > li:nth-child(3){
    border: 1px solid #1AEF49;
}
.list > li:nth-child(4){
    border: 1px solid red;
}

.container > div{
    margin: 40px 0 0 0;
}