.container{
    max-width: 342px;
    width: 100%;
    height: 295px;
    padding: 198px 0 0 0;
    margin: 10px;
}
.box_text{
    background-image: url("../../../assets/img/last_news_textbg.png");
    max-width: 332px;
    width: 100%;
    height: 71px;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    color: #FFFFFF;
    text-align: center;
    padding: 3% 3%;
    margin: 0 auto;
}
.box_text > p{
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}